import React from 'react';
import Users from "./../data/users.json";

class Login extends React.Component{
	
	constructor(props){
		super(props);
		
		this.state = {user: {username: "", password: ""} };
	}
   

     onChangeHandler = (e) => {
        const _user = {...this.state.user, [e.target.name] : e.target.value}
        this.setState({user: _user});
    }

    loginHandler = (e) => {
        e.preventDefault();
		const jsonString = JSON.stringify(Users);
		const _users = JSON.parse(jsonString);
		
		console.log(_users);

		const foundUser = _users.find(element => element.username === this.state.user.username &&  element.password === this.state.user.password);

        if(foundUser !== undefined){
            localStorage.setItem("token", foundUser.username);
            this.props.history.push('/account');
        }
        
    }

    render(){

		return (
			<>
				<form>
					
					<div className="container">
						<label for="uname">Username</label>
						<input type="text" value={this.state.user.username} onChange={this.onChangeHandler} placeholder="Enter Username" name="username" required />

						<label for="psw">Password</label>
						<input type="password" value={this.state.user.password} onChange={this.onChangeHandler} placeholder="Enter Password" name="password" required />
						
						<button type="submit" onClick={this.loginHandler}>Login</button>

					</div>

				</form>
			</>
		);
	}
}

export default Login;

